@import 'abstract/variables';
@import 'abstract/mixins';
@import 'base/base';
@import 'base/typography';
@import 'base/utilities';


.container {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
}

nav {
  padding: 4rem;
  grid-column: 3 / 4;
  justify-self: end;

  a {
    margin: 0 2rem 0 0;
    padding: 1rem;
    background: #23813f;
    text-decoration: none;
    font-size: 1.6rem;
    color: #fff;
    border-radius: 5px;
  }
}

.title {
  grid-column: 1 / 3;
  grid-row: 2;
  align-self: center;
  font-size: 5rem;
  padding: 3rem 9rem;
}

@keyframes fadeIn {
  0% {opacity: 0;}
  100% {opacity: 1;}
}